import { DiffOutlined, FilterOutlined, SearchOutlined } from "@ant-design/icons";
import { Button, Divider, Grid, Steps, Row, Col, Form, Select, Tooltip, PageHeader } from "antd";
import Flex from "components/shared-components/Flex";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import { ACTIONS, CHILDREN, DATE_FORMAT, LIGHT_NAVY_BLUE_COLOR, SORT_DIRECTIONS } from "constants/CommonConstants";
import { ADMIN_ROLES, ROLES } from "constants/ManageEmployeeConstants";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import employeeService from "services/EmployeeService";
import Utils from "utils";
import { showLoader } from "redux/actions/Loader";
import { Link, useHistory } from "react-router-dom";
import { APP_PREFIX_PATH } from "configs/AppConfig";
import dashboardServices from "services/DashboardServices";
import {
  setCountryForReserveEmployee,
  setDataForReserveEmployee,
  setReserveForAccountId,
} from "../../../../redux/actions/MyTeams";
import { resetFilterSort, updateFilterSort } from "redux/actions";
import { headings } from "constants/ReserveEmployeeConstants";

const { useBreakpoint } = Grid;

const ReserveEmployees = ({ location }) => {
  const params = new URLSearchParams(location.search).get("to");
  const dispatch = useDispatch();
  const previousData = useSelector(state => state.myTeams?.reserveEmployeeData);
  const previousTableFilter = useSelector(state => state.theme?.filters);
  const previousTableSort = useSelector(state => state.theme?.sort);
  // const [selectedEmployees, setSelectedEmployees] = useState([]) For Future use BULK Update
  const [allocateEmployeeData, setAllocateEmployeeData] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [sortedInfo, setSortedInfo] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const [currentPageSize, setCurrentPageSize] = useState(10);
  const isMobile = !Utils.getBreakPoint(useBreakpoint()).includes("sm");
  const [tableKey, setTableKey] = useState(0);
  const role = useSelector(state => state?.myTeams?.userInfo?.role);
  const [quickActionBtns, setQuickActionBtns] = useState([]);
  const [dynamicColumns, setDynamicColumns] = useState([]);
  let searchInput = useRef(null);
  const { Step } = Steps;
  const [selectedCountry, setSelectedCountry] = useState(null);
  const locations = useSelector(state => state.myTeams.allLocations);
  const previousCountry = useSelector(state => state.myTeams.reserveEmployeeCountry);
  const getMessage = useIntlMessage();
  const { Option } = Select;
  const [form] = Form.useForm();
  const history = useHistory();

  useEffect(() => {
    if (selectedCountry) {
      handleOnSearch(params);
    }
    if (previousCountry && params === previousData.params) {
      setDynamicColumns(previousData.columns);
      setQuickActionBtns(previousData.quickActions);
      setAllocateEmployeeData(previousData.data);
      setFilteredInfo(previousTableFilter);
      setSortedInfo(previousTableSort);
    }
  }, [selectedCountry]);

  useEffect(() => {
    return () => {
      if (history.location.pathname.includes("allocateemployees") === false) {
        dispatch(resetFilterSort());
      }
    };
  }, []);

  const getColumnSearchProps = field => {
    let filterObj = {
      filterIcon: filtered => (
        <SearchOutlined
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
          id={`manage-emp-search-btn-${field.key}`}
        />
      ),
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: option.title,
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
          id={`manage-emp-search-btn-${field.key}`}
        />
      );
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          field={field}
          searchInput={searchInput}
          handleReset={handleReset}
          label={field.title}
          inputId={`manage-emp-input-${field.key}`}
          resetBtnId={`manage-emp-reset-btn-${field.key}`}
          searchBtnId={`manage-emp-search-btn-${field.key}`}
          dataSource={allocateEmployeeData}
        />
      );

      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const valuee = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
        }
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const handleChange = (pagination, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
    dispatch(updateFilterSort({ filters: filters, sort: sorter }));
    const { current, pageSize } = pagination;
    setCurrentPageSize(pageSize);
    setCurrentPage(current);
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    // setSearchText(selectedKeys[0]);
    // setsearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters, confirm) => {
    setTableKey(tableKey => tableKey + 1);
    clearFilters?.();
    // setSearchText("");
    confirm?.();
    dispatch(resetFilterSort());
  };

  const generateDynamicColumns = columns => {
    return columns?.map(field => ({
      title: (
        <Tooltip title={field.title} placement="top">
          <span>{field.title}</span>
        </Tooltip>
      ),
      dataIndex: field.key,
      key: field.key,
      sorter: (a, b) => field.sortable && Utils.antdTableSorter(a, b, field.key),
      sortDirections: SORT_DIRECTIONS,
      filteredValue: filteredInfo[field.key] || null,
      ellipsis: true,
      sortOrder: field.sortable && sortedInfo.columnKey === field.key ? sortedInfo.order : null,
      ...(field.searchable ? getColumnSearchProps(field) : null),
      width: field.width || 150,
      render: (_, record) => {
        if (field.type === "date") {
          return record[field.key] ? moment(record[field.key]).format(DATE_FORMAT) : "";
        }
        return (
          <Tooltip title={record[field.key]} placement="top">
            <span id={field.key}>{record[field.key]}</span>
          </Tooltip>
        );
      },
    }));
  };

  const setReserveForAccountID = id => {
    dispatch(setReserveForAccountId(id));
  };

  const manageEmployeeColumns = [
    ...generateDynamicColumns(dynamicColumns ? dynamicColumns : []),
    {
      title: ACTIONS.text,
      dataIndex: ACTIONS.value,
      key: ACTIONS.value,
      width: ROLES.includes(role) ? 160 : 120,
      fixed: isMobile ? "" : "right",
      render: (_, record) => {
        let status = (record?.allocationStatus).toLowerCase();
        let to =
          params === "reserved" && status === "shadow"
            ? `${APP_PREFIX_PATH}/manage/allocateemployees?to=${params}&empID=${record.employeeId}&empStatus=${status}${
                record?.reservedForAccountId ? `&reservedForAccountId=${record?.reservedForAccountId}` : ""
              }`
            : `${APP_PREFIX_PATH}/manage/allocateemployees?to=${params}&empID=${record.employeeId}${
                record?.reservedForAccountId ? `&reservedForAccountId=${record?.reservedForAccountId}` : ""
              }`;
        return (
          <Flex className="mr-2">
            {role !== ADMIN_ROLES.ADMIN.key && role !== "RMG" ? (
              <Tooltip title={`move resource to ${params}`}>
                <Button
                  size="small"
                  className="ml-1"
                  id="manage-emp-allocation"
                  onClick={() => setReserveForAccountID(record?.reservedForAccountId)}
                >
                  <Link to={to} onClick={() => history.push(to)}>
                    <DiffOutlined className="mr-1" />
                    {params === "reserved"
                      ? "Reserve"
                      : params === "confirmed"
                      ? "Confirm"
                      : params.charAt(0).toUpperCase() + params.slice(1)}
                  </Link>
                </Button>
              </Tooltip>
            ) : null}
          </Flex>
        );
      },
    },
  ];

  const handleOnSearch = params => {
    form
      .validateFields()
      .then(() => {
        if (selectedCountry !== null) {
          getEmployeeList(params, selectedCountry);
        }
      })
      .catch(e => {
        console.error(e);
      });
  };
  const dispatchEvents = (data, quickActions, columns, params) => {
    dispatch(setDataForReserveEmployee(data, quickActions, columns, params));
    dispatch(setCountryForReserveEmployee(selectedCountry));
  };

  const getEmployeeList = async (allocationStatus, selectedCountry) => {
    try {
      dispatch(showLoader(true));
      const response = await dashboardServices.getDashboardAllocateEmployeesTo(
        allocationStatus.toUpperCase(),
        selectedCountry,
      );
      const { columns, data, quickActions } = response;
      setQuickActionBtns(quickActions);
      if (columns.length > 1) {
        setDynamicColumns(columns);
      }
      const employeeList = employeeService.prepareEmployeeList(data);
      dispatchEvents(employeeList, quickActions, columns, params);
      setAllocateEmployeeData(employeeList);
    } catch (e) {
      console.error("Error in getting the employees information", e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  /* For future use BULK UPDATE 
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        setSelectedEmployees(selectedRowKeys)
      },
      onSelect: (record, selected, selectedRows) => {
        console.log(record, selected, selectedRows);
      },
      onSelectAll: (selected, selectedRows, changeRows) => {
        console.log(selected, selectedRows, changeRows);
      },
    };

    const onBulkAllocateClick = () =>{
      console.log(selectedEmployees,location)
      history.push(`bulkallocate?to=${params}&empIDs=${selectedEmployees}`)
    }
    */

  return (
    <>
      <PageHeader className="p-2 pl-2" title={getMessage(headings[params.toLowerCase()])} />
      <Divider />

      <Steps current={0}>
        <Step title="Step 1" description="Select Employee" />
        <Step title="Step 2" description="Allocate Employee" />
      </Steps>
      {/* <Button className="mt-3">
        <Link to={`${APP_PREFIX_PATH}/dashboard`}>Previous</Link>
      </Button> */}

      <Divider />
      {/* For Future use BULK UPDATE */}
      {/* <Row justify="end">
          <Button onClick={()=>onBulkAllocateClick()} disabled={selectedEmployees.length <= 1} type="primary" className="mb-3 ">Bulk Allocate</Button>
        </Row> */}
      <Row align="middle" gutter={[20, 20]} className="mb-2" justify="end">
        <Col xs={24} md={12} lg={6} xl={6}>
          <Form name="basic" form={form}>
            <Form.Item
              name="manage-project-select-account"
              label={getMessage("common.label.select-country")}
              rules={[{ required: true, message: "Select Country" }]}
              className="mb-0"
              initialValue={previousCountry && params === previousData.params ? previousCountry : null}
            >
              <Select
                className="w-100"
                placeholder={getMessage("common.label.select-country")}
                name={"manage-emp-select-country"}
                id="manage-emp-select-country"
                onChange={setSelectedCountry}
                showArrow
                showSearch
                optionFilterProp={CHILDREN}
              >
                {locations?.map(account => {
                  return (
                    <Option key={account?.country} value={account?.country}>
                      {account?.country}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Form>
        </Col>
      </Row>
      <div style={{ position: "relative" }}>
        {!!allocateEmployeeData.length && !!quickActionBtns.length && (
          <div className="is-mobile">
            <div className="floating-btn floating-buttons">
              {quickActionBtns.map(btn => {
                return (
                  <Tooltip title={btn.description} placement="top">
                    <Button
                      type="primary"
                      className="floating-btn-circle my-1"
                      onClick={() => Utils.scrollIntoColumn(btn.key)}
                    >
                      {btn.title}
                    </Button>
                  </Tooltip>
                );
              })}
            </div>
          </div>
        )}

        <TableComponent
          className={"pl-4 bg-white border rounded"}
          tableKey={tableKey}
          rowKey={record => record.key}
          columns={manageEmployeeColumns}
          data={allocateEmployeeData}
          scroll={{ x: 1500 }}
          handleChange={handleChange}
          tableSize="small"
          showDropdown={false}
          // rowSelection={{...rowSelection}} For future use BULK UPDATE
          paginationHandler={{
            defaultPageSize: currentPageSize,
            current: currentPage,
          }}
          showSorterTooltip={false}
        />
      </div>
    </>
  );
};

export default ReserveEmployees;
